import { Component } from '@angular/core';
import { NavController, NavParams, AlertController, Alert } from 'ionic-angular';
import { Carro } from '../../domain/carro/carro';
import { Http } from '@angular/http';
import { HomePage } from '../home/home';
import { Agendamento } from '../../domain/agendamento/agendamento';

@Component({
  templateUrl: 'cadastro.html'
})
export class CadastroPage {

  public carro: Carro
  public precoTotal: number

  public data: string = new Date().toISOString()
  public agendamento: Agendamento
  private _alerta: Alert

  constructor(public navCtrl: NavController, public navParams: NavParams, private _http: Http, private _alertCtrl: AlertController) {
    this.carro = this.navParams.get('carro')
    this.precoTotal = this.navParams.get('precoTotal')

    this.agendamento = new Agendamento(this.carro, this.precoTotal)

    this._alerta = this._alertCtrl.create({
      title: 'Alerta',
      buttons: [{ text: 'ok', handler: () => this.navCtrl.setRoot(HomePage) }]
    })


  }

  /*ionViewDidLoad() { //tipo onInit mas o onInit recarrega, o ionViewDidLoadcarrega apenas a primeira vez
    console.log('ionViewDidLoad CadastroPagePage');
  }*/

  agenda() {
    this._http
      .get(`https://aluracar.herokuapp.com/salvarpedido?carro=${this.agendamento.carro.nome}&nome=${this.agendamento.nome}&preco=${this.agendamento.valorTotal}&endereco=${this.agendamento.endereco}&email=${this.agendamento.email}&dataAgendamento=${this.agendamento.data}`)
      .toPromise()
      .then(() => {
        this._alerta.setSubTitle('Agendamento realizado com sucesso!')
        this._alerta.present()
      })
      .catch(erro => {
        this._alerta.setSubTitle('Não foi possível realizar o agendamento, tente mais tarde!')
        this._alerta.present()
      })
  }


}



